import Web3 from "web3"
export const signCall = (eth, to, data, value) => {
  if (!eth) eth = window.ethereum
  if (!value) {
    return eth.request({
      method: 'eth_sendTransaction',
      params: [{
        from: eth.selectedAddress,
        to,
        data
      }]
    })
  }
  return eth.request({
    method: 'eth_sendTransaction',
    params: [{
      from: eth.selectedAddress,
      to,
      data,
      value: Web3.utils.toHex(value)
    }]
  }) 
}