export const toBN = (value, decimals) => {

	if (!value || !decimals) return Web3.utils.toBN('0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff')
	const num = Number(value * 10 ** decimals);
	// 转成字符串 1,000,000,000,000,000.1234 去掉,及小数点后面部分
	const s = num.toLocaleString().replaceAll(',', '').split('.')[0];
	return Web3.utils.toBN(s);

}

export const BN_to_str = (value, decimals) => {
	return Number(value / 10 ** decimals).toFixed(decimals);
}

export const injected = new InjectedConnector({
	supportedChainIds: [
		1, // Mainet
		3, // Ropsten
		4, // Rinkeby
		5, // Goerli
		42, // Kovan
		65, // OEC
		66, // 
		256, //hecotest
	],
})



export const getBalance = (contract, address) => {
	return contract.methods.balanceOf(address).call()
}

export const verifyChainId = async (provider, eth) => {
	const ethChainId = parseInt(eth.chainId, 16)
	const providerChainId = await provider.eth.getChainId()
	console.log(ethChainId, providerChainId)
	if (ethChainId === providerChainId) return true;
	const params = chains_params[providerChainId]
	console.log('params', params)
	await eth.request({ method: 'wallet_addEthereumChain', params })
		.then(() => console.log('Success'))
		.catch((error) => console.log("Error", error.message));
	return false
}

export const verifyAllown = async (contract, address, router_addr) => {
	return await contract.methods.allowance(address, router_addr).call();
}