import React, { useMemo } from 'react'
import classnames from 'classnames'
import uuid from 'uuid/dist/v4'
import { FormattedMessage, injectIntl } from 'react-intl'
import { injected } from '../../utils/utils'
import { useWeb3React } from '@web3-react/core'
import { Link } from 'react-router-dom'
import { Dropdown, } from 'antd'
import styles from './index.less'

const router = [{
  path: '/home',
  name: '首页',
  key: 'home'
}, {
  path: '/gunpool',
  name: '机枪池',
  key: 'gp'
}, {
  path: '/bridge',
  name: '跨链桥',
  key: 'cb'
}, {
  path: '/pledgepool',
  name: '质押池',
  key: 'mp'
}, {
  path: '/dividendpool',
  name: '分红池',
  key: 'br'
}, {
  path: '/info',
  name: '信息',
  key: 'info'
}]

const Header = ({
  location: {
    pathname
  },
  ...props
}) => {
  console.log('props', props)
  const { activate, active, account } = useWeb3React()

  const handleContect = async () => {
    await activate(injected, walletError => {
      if (walletError.message.includes('user_canceled')) {
        return alert('You canceled the operation, please refresh and try to reauthorize.')
      }
      alert(`Failed to connect: ${walletError.message}`)
    })
  }

  const userInfo = useMemo(() => {
    if (!active) return (<div className={styles.connect} onClick={handleContect}>链接</div>);
    return `账号：${account.replace(0, 10, '...')}`
  }, [active, account])

  const IsPC = useMemo(() => {
    const userAgentInfo = window.navigator.userAgent;
    const Agents = ["Android", "iPhone",
          "SymbianOS", "Windows Phone",
          "iPad", "iPod"];
    let flag = true;
    for (let v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
        flag = false;
        break;
      }
    }
    return flag;
  }, [])

  const menu = useMemo(() => {
    const menuRouter = router.map(item => (
      <Link
        className={
          classnames({[styles.active]: item.path === pathname})
        }
        key={uuid()}
        to={item.path}
      >
        <FormattedMessage id={item.key}/>
      </Link>
    ))
    if (IsPC) {
      return menuRouter
    }
    return (
      <Dropdown
        overlay={<>{menuRouter}</>}
        overlayClassName={styles.mobileMenu}
        trigger={['click']}
        placement="bottomCenter"
        getPopupContainer={(triggerNode) => triggerNode.parentNode}
      >
        <div>菜单</div>
      </Dropdown>
    )

  }, [IsPC])

  return (
    <div className={styles.headerWrapper}>
      <div className={styles.logo}>logo</div>
      <div className={styles.menu}>
        {menu}
      </div>
      <div className={styles.user}>{userInfo}</div>
    </div>
  )
}

export default injectIntl(Header)
