import React, { useEffect, useState, useMemo } from 'react'
import uuid from 'uuid/dist/v4'
import { Tabs, Table, Divider, Modal, Input, message } from 'antd'
import { chains_params, airDropAbi, erc20abi } from '../../../public/Web3'
import { signCall } from '../../utils/ethRequest'
import { toBN, BN_to_str, verifyChainId, verifyAllown } from '../../utils/utils'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { TabPane } = Tabs;

const AmountModal = ({
  visible,
  type,
  item,
  provider,
  handleOk,
  handleCancel,
  airDropAddr
}) => {
  const [amount, setAmount] = useState()
  const [balance, setBalance] = useState(null)
  const [error, setError] = useState(false)

  useMemo(async () => {
    if (!item || !type) return;
    const { decimals, lpToken, pid } = item;
    const ethereum = window.ethereum;
    if (type === 'zy') {
      const contract = new provider.eth.Contract(erc20abi, lpToken)
      return await contract.methods.balanceOf(ethereum.selectedAddress).call().then(result => {
        const balance = BN_to_str(result, decimals)
        setBalance(balance)
      })
    }

    if (type === 'jy') {
      const contract = new provider.eth.Contract(airDropAbi, airDropAddr)
      return await contract.methods.userInfo(pid,ethereum.selectedAddress).call().then(result => {
        const balance = BN_to_str(result.amount, decimals)
        setBalance(balance)
      })
    }

  }, [item, type])

  const handleChange = (value) => {
    setError(false)
    if (Number(value) > Number(balance)) return setError(true)
    setAmount(value)
  }
  
  const onOk = () => {
    if (!amount) return handleCancel()
    handleOk(Number(amount))
  }

  return (
    <Modal
      visible={visible}
      onOk={onOk}
      onCancel={() => {
        setAmount('')
        setError(false) 
        handleCancel()
      }}
      getContainer={false}
      closable={false}
    >
      <div className={styles.amount}>
        <Input value={amount} onChange={(e) => { handleChange(e.target.value) }} />
        <div onClick={() => { handleChange(balance) }}>剩余：{balance}</div>
        {
          error ? <div className={styles.errorTip}>输入数量不能大于剩余数量</div> : null
        }
      </div>
      
    </Modal>
  )
}


const Pledgepool = (props) => {

  const columns = [{
    title: 'LP',
    dataIndex: 'lpName',
    width: '20%',
  }, {
    title: 'TVL',
    dataIndex: 'TVL',
    width: '20%',
    render: (num, record) => BN_to_str(num, record.decimals)
  }, {
    title: 'APY',
    dataIndex: 'APY',
    width: '20%'
  }, {
    title: '待提取',
    dataIndex: 'pendingAmount',
    width: '20%'
  }, {
    title: '操作',
    dataIndex: 'operation',
    width: '20%',
    render: (_, record, index) => {
      return (
        <div className={styles.group}>
          <div onClick={() => {handleCtrl('zy', record)}}>质押</div>
          <Divider type="vertical" />
          <div onClick={() => {handleCtrl('jy', record)}}>解押</div>
          <Divider type="vertical" />
          <div onClick={() => {handleCtrl('tq', record)}}>提取收益</div>
        </div>
      )
    }
  }]

  const [visible, setVisible] = useState(false)
  const [item, setItem] = useState(null)
  const [type, setType] = useState('')
  const [tabKey, setTabKey] = useState("256")
  const [ctrlFlag, setCtrlFlag] = useState(true)
  const [tableData, setTableData] = useState([])
  const [pendingAll, setPendingAll] = useState(null)

  const { myContract, myProvider,  airDropAddr } = useMemo(() => {
    const { provider, airDropAddr } = chains_params[tabKey]
    const contract = new provider.eth.Contract(airDropAbi, airDropAddr)
    return { myContract: contract, myProvider: provider, airDropAddr}
  }, [tabKey])


  useMemo(async () => {

    if (!ctrlFlag) return
    const data = await myContract.methods.getPoolInfos().call()
    const ethereum = window.ethereum
    const tableData = await Promise.all(data.map(async (ele, i)=> {
      
      return (async ()=>{
        const { accCtPerShare,allocPoint, lastRewardBlock, lpName, lpToken } = ele;
        const lpContract = new myProvider.eth.Contract(erc20abi, lpToken) // 当前tab页签的provider
        const decimals = await lpContract.methods.decimals().call()
        const TVL = await lpContract.methods.balanceOf(airDropAddr).call()
        const pendingAmount = await myContract.methods.pending(i, ethereum.selectedAddress).call()

        return { accCtPerShare,allocPoint, lastRewardBlock, lpName, lpToken, TVL, pid: i, decimals, pendingAmount }
      })()
     
    }))
    
    setTableData(tableData)
  }, [tabKey, ctrlFlag])

  useMemo(async () => {

    const ethereum = window.ethereum
    if (!ethereum.selectedAddress) return '';

    console.log(ethereum.selectedAddress)
    const value = await myContract.methods.pendingAll(ethereum.selectedAddress).call()
    console.log('value', value)
    setPendingAll(value)
  }, [tabKey, window.ethereum.selectedAddress])

  const handleCtrl = async (type, data) => {
    const { lpToken, pid } = data;
    const ethereum = window.ethereum
    const contract = new myProvider.eth.Contract(erc20abi, lpToken)
    const allown = await verifyAllown(contract, ethereum.selectedAddress, airDropAddr)
    const vaildChainId = await verifyChainId(myProvider, ethereum)
    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }

    if (type === 'zy') {
      if (allown === '0') {
        return signCall(
          ethereum,
          lpToken,
          contract.methods.approve(airDropAddr,toBN()).encodeABI()
        )
      }
    }
    
    if (type !== 'tq') {
      setItem(data)
      setType(type)
      setVisible(true)
    }

    if (type === 'tq') {
      const data = myContract.methods.claim(pid).encodeABI()
      signCall(ethereum, airDropAddr, data)
    }
   
  }

  const handleOk = async (money) => {
    const { pid, decimals, lpToken } = item
    const ethereum = window.ethereum
    const amount = toBN(money, decimals)

    const data = type === 'zy' ? myContract.methods.deposit(pid, amount).encodeABI() : myContract.methods.withdraw(pid, amount).encodeABI()

    const vaildChainId = await verifyChainId(myProvider, ethereum)
    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }

    ctrlMoney(ethereum, airDropAddr, data).then(result => {
      if (result) {
        message.success('交易成功')
        setTimeout(() => {
          setCtrlFlag(true)
        }, 5000);
        setVisible(false)
      }
    })
  }

  const takeAll = async () => {
    const ethereum = window.ethereum
    const data = myContract.methods.claimAll().encodeABI()
    const vaildChainId = await verifyChainId(myProvider, ethereum)
    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }
    signCall(ethereum, airDropAddr, data)
  }

  const ctrlMoney = (eth, addr,data ) => {
    return signCall(eth,addr,data)
  }

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.pledgepool}`}>
        <Tabs
          size="large"
          activeKey={tabKey}
          onChange={key => {
            setTabKey(key)
            setCtrlFlag(true)
          }}
        >
          <TabPane tab="HECO" key="256" />
          <TabPane tab="OEC" key="65" />
          <TabPane tab="ETH" key="4" />
        </Tabs>
        <div className={styles.allTQ}>
          <div>待提取总量: {pendingAll}</div>
          <div onClick={takeAll}>提取全部收益</div>
        </div>
       
        <Table 
          rowKey={() => uuid()}
          columns={columns}
          dataSource={tableData}
          pagination={false}
        />
        <AmountModal
          visible={visible}
          type={type}
          item={item}
          provider={myProvider}
          airDropAddr={airDropAddr}
          handleOk={handleOk}
          handleCancel={() => {setVisible(false)}}
        />
      </div>
    </>
  )
}

export default Pledgepool
