import React, { useState, useMemo } from 'react'
import Web3 from 'web3'
import uuid from 'uuid/dist/v4'
import moment from 'moment'
import { useWeb3React } from '@web3-react/core'
import { Select, Input, Button, Checkbox, Modal, Table, Divider, message, Radio } from 'antd'
import { erc20abi, cubePollAbi, SignContract, chains_params } from '../../../public/Web3'
import { signCall } from '../../utils/ethRequest'
import { toBN, injected, BN_to_str, getBalance, verifyChainId, verifyAllown } from '../../utils/utils'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'


const { Option } = Select;
const pageSize = 10;
const stateMapping = {
  "0": 'pending',
  "1": 'finished',
  '2': 'failed',
  '3': 'canceled'
}



const HistoryModal = ({
  visible,
  provider,
  contract,
  item,
  onCancel
}) => {
  const [total, setTotal] = useState()
  const [currentPage, setCurrentPage] = useState(0)
  const [tableData, setTableData] = useState([])
  const [confirmVisible, setConfirmVisible] = useState(false)
  const [radioValue, setRadioValue] = useState("token")
  const [confirmItem, setConfirmItem] = useState(null)
  const [confirmType, setConfirmType] = useState('')
  const columns = [{
    title: '编号',
    dataIndex: 'nonce'
  },{
    title: 'block',
    dataIndex: 'momentTimestamp'
  },{
    title: 'toChain',
    dataIndex: 'to_chain_id'
  }, {
    title: 'amount',
    dataIndex: 'amount',
    render: (num, record) => BN_to_str(num, record.decimals)
  }, {
    title: 'state',
    dataIndex: 'dyanmicState',
    render: (text) => stateMapping[text]
  }, {
    title: '操作',
    dataIndex: 'operation',
    render: (_, record, index) => {
      const { dyanmicState } = record
      let btn = ''
      switch (stateMapping[dyanmicState]) {
        case 'pending':
          btn = (
            <>
              <div onClick={() => { handleConfirmModal('extract', record) }}>提取</div>
              <Divider type="vertical" />
              <div onClick={() => { handleConfirmModal('cancel', record) }}>取消</div>           
            </>
          )
          break;
        case 'canceled': 
          btn = <div onClick={() => { handleConfirmModal('retrieve', record) }}>取回</div>
          break;
        default:
          btn = null
          break;
      }
      return (<div className={styles.group}>{btn}</div>)
    }
  }]

  const getTableData = async (current) => {
    const ethereum = window.ethereum;
    const { token, liquidity_token } = item
    const count = current !== undefined ? total: await contract.methods.getUserHistoryLength(ethereum.selectedAddress, token).call()
    const currentPage = current !== undefined ? current : Math.ceil(count / pageSize) - 1
    const currentPageSize = count - currentPage*pageSize

    const data = await contract.methods.getUserHistory(ethereum.selectedAddress, token, currentPage === -1 ? 0 : currentPage, currentPageSize).call()

    const tableData = await Promise.all(data.map(async (ele)=>{
      return (async ()=>{
        const { transaction_hash } = ele;
        const { amount, block_number, decimals,from, from_chain_id, is_bridge_redeem, nonce, state, to, to_chain_id  } = ele.transaction;
        const { provider: toChainProvider, contract: toChainContract} = chains_params[to_chain_id]
        const { timestamp } = await provider.eth.getBlock(block_number)
        const momentTimestamp = moment.unix(timestamp).format('yyyy-MM-DD hh:mm:ss')
        const cubePoolCntr = new provider.eth.Contract(cubePollAbi, liquidity_token) // 当前链 web3实例，abi：cubepoll，token：外面带入
        const toToken = await cubePoolCntr.methods.chain_token(to_chain_id).call()
        const toLiquidityToken = await toChainContract.methods.poolFor(toToken).call()
        const toCubePoolCntr = new toChainProvider.eth.Contract(cubePollAbi, toLiquidityToken) // 目标链web实例，abi：cubepoll，token：目标liquidity_token
        let dyanmicState = state
        if (state === '0') {
          const { state } = await toCubePoolCntr.methods.all_transactions(transaction_hash).call()
          dyanmicState = state
        }
        return { amount, momentTimestamp, block_number, decimals, from, from_chain_id, is_bridge_redeem, nonce, state, to, to_chain_id, transaction_hash, dyanmicState, toToken, toLiquidityToken, cubePoolCntr, toCubePoolCntr, token, liquidity_token }
      })()
    }))
    if (current === undefined) {
      setTotal(count)
    }
    
    setCurrentPage(currentPage)
    setTableData(tableData)
  }

  useMemo(async () => {
    if (!visible) return
 
    getTableData()

  }, [visible])

  const handleTableChange = (page) => {
    getTableData(page - 1)
  }

  const handleConfirmModal = async (type, data) => {
    const ethereum = window.ethereum
    const { to_chain_id } = data
    const { provider:　toChainProvider } = chains_params[to_chain_id]
    const vaildProvider = type === 'retrieve' ? provider : toChainProvider
    const vaildChainId = await verifyChainId(vaildProvider, ethereum)
    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }

    setConfirmVisible(true)
    setConfirmItem(data)
    setConfirmType(type)
  }

  const getWithDraw = (value) => {
    setRadioValue(value)
  }

  const handleTakeOk = async () => {
    const {
      transaction_hash,
      is_bridge_redeem,
      from_chain_id,
      from,
      to,
      amount,
      decimals,
      nonce,
      block_number,
      liquidity_token,
      toLiquidityToken,
      cubePoolCntr,
      toCubePoolCntr
    } = confirmItem
  
    const ethereum = window.ethereum; 
    const confirmContract = confirmType === 'retrieve' ? cubePoolCntr : toCubePoolCntr
    const withdraw_token = radioValue === 'token' ? true : false
    const liquidityToken = confirmType === 'retrieve' ? liquidity_token : toLiquidityToken
    const sigs = confirmType === 'retrieve' ? await SignContract.methods.getCancelSigns(transaction_hash).call() : await SignContract.methods.getConfirmSigns(transaction_hash).call()
 
    let data
    switch (confirmType) {
      case 'extract':
        data = confirmContract.methods.bridge_withdraw(is_bridge_redeem, from_chain_id, from, to, amount, decimals, nonce, block_number, withdraw_token, sigs).encodeABI()
        break;
      case 'cancel':
        data = confirmContract.methods.bridge_cancel(is_bridge_redeem, from_chain_id, to, amount, decimals, nonce, block_number).encodeABI()
        break;
      default:
        data = confirmContract.methods.bridge_fallback(transaction_hash, withdraw_token, sigs).encodeABI()
        break;
    }

    signCall(ethereum, liquidityToken, data).then(result => {
      if (result) {
        setConfirmVisible(false)
        window.setTimeout(() => {getTableData(currentPage)}, 5000)
      }
    })
  }


  return (
    <Modal
      width={650}
      title="历史记录"
      visible={visible}
      onCancel={onCancel}
      footer={null}
      getContainer={false}
    >
      <Table 
        rowKey={() => uuid()}
        columns={columns}
        dataSource={tableData}
        pagination={{
          pageSize, 
          total,
          current: currentPage + 1,
          onChange: handleTableChange
        }}
      />
      <ConfirmModal
        visible={confirmVisible}
        type={confirmType}
        handleOk={handleTakeOk}
        getWithDraw={getWithDraw}
        handleCancel={() => { setConfirmVisible(false)}}
      />
    </Modal>
  )
}

const ConfirmModal = ({
  visible,
  handleCancel,
  handleOk,
  getWithDraw,
  type
}) => (
  <Modal
    visible={visible}
    onCancel={handleCancel}
    onOk={handleOk}
  >
    {
      type === 'cancel' ? '是否取消？' : (
        <Radio.Group onChange={(e) => { getWithDraw(e.target.value)}} defaultValue='token'>
          <Radio value="token">提取token</Radio>
          <Radio value="liuidity">提取liuidity</Radio>
        </Radio.Group>
      )
    }
    
  </Modal>
)

const Bridge = (props) => {
  const [visible, setVisible] = useState(false)
  const [formChain, setFormChain] = useState('')
  const [toChain, setToChain] = useState('')
  const [toChainId, setToChainId] = useState('')
  const [toChainProvider, setToChainProvider] = useState('')
  const [toChainContract, setToChainContract] = useState('')
  const [tokenList, setTokenList] = useState([])
  const [tokenItem, setTokenItem] = useState(null)
  const [selectedToken, setSelectedToken] = useState('')
  const [errorTip, setErrorTip] = useState(false)
  const [address, setAddress] = useState('')
  const [amount, setAmount] = useState('')
  const [balance, setBalance] = useState(null)
  const [checkedType, setChecdkType] = useState(false)
  const [error, setError] = useState(false)

  const { activate, active } = useWeb3React()

  const handleContect = async () => {
    await activate(injected, walletError => {
      if (walletError.message.includes('user_canceled')) {
        return alert('You canceled the operation, please refresh and try to reauthorize.')
      }
      alert(`Failed to connect: ${walletError.message}`)
    })
  }

  // 获得当前链实例
  const { myContract, myProvider, cubeAddr } = useMemo(() => {
    if (!formChain) return { myContract: null, myProvider: null, cubeAddr: null }
    const { contract, provider, cubeAddr } = chains_params[formChain]
    return { myContract: contract, myProvider: provider, cubeAddr }
  }, [formChain])

  useMemo(() => {
    if (!myContract) return null;

    myContract.methods.getBridgePoolInfos().call().then((result) => {
      const list = result.map(item => {

        const { token,equity, symbol,liquidity_decimals, apy, token_decimals, liquidity_token  } = item;
        return {
          token,
          equity,
          symbol,
          liquidity_decimals,
          apy,
          token_decimals,
          liquidity_token
        }
      })
      // setAddress(myContract._address)
      setTokenList(list)
    })
    
  }, [formChain])

  useMemo(() => {
    if (!active) {
      handleContect()
    }
  }, [])

  useMemo(() => {
    const ethereum = window?.ethereum
    setAddress(ethereum.selectedAddress)
  }, [active])

  useMemo(async () => {
    if (!selectedToken) return;
    const tokenItem = tokenList.find(item => item.token === selectedToken)
    const ethereum = window.ethereum;
    const { token, liquidity_token, token_decimals, liquidity_decimals } = tokenItem
    const cubeContract = !checkedType ? new myProvider.eth.Contract(erc20abi, token) : new myProvider.eth.Contract(erc20abi, liquidity_token);
    const decimals = !checkedType ? token_decimals : liquidity_decimals
    const weth = !checkedType ? await myContract.methods.weth().call() : undefined
    setTokenItem(tokenItem)
    if (token === weth) {
      myProvider.eth.getBalance(ethereum.selectedAddress).then(result => {
        const balance = BN_to_str(result, decimals)
        setBalance(balance)
      })
    } else {
      getBalance(cubeContract, ethereum.selectedAddress).then(result => {
        const balance = BN_to_str(result, decimals)
        setBalance(balance)
      })
    }
    
  }, [selectedToken, checkedType])

  const handleToChain = async (val) => {
    setErrorTip(false)
    if (val === formChain) {
      return setErrorTip(true)
    }

    const { contract, provider, chainId } = chains_params[val]

    setToChainId(chainId)
    setToChainProvider(provider)
    setToChainContract(contract)
    return setToChain(val)
  }

  const handleChange = (value) => {
    setError(false)
    if (Number(value) > Number(balance)) return setError(true)
    setAmount(value)
  }

  const handleCommit = async () => {
    const ethereum = window.ethereum;
    const { token_decimals, liquidity_decimals, token, liquidity_token } = tokenItem
    const decimals = !checkedType ? token_decimals : liquidity_decimals
    const typeToken = !checkedType ? token : liquidity_token
    const toBNAmount = toBN(amount, decimals)
    const ercCntr = !checkedType ? new myProvider.eth.Contract(erc20abi, token) : new myProvider.eth.Contract(erc20abi, liquidity_token)
    const vaildChainId = await verifyChainId(myProvider, ethereum)
    const allown = await verifyAllown(ercCntr, ethereum.selectedAddress, cubeAddr)
    const weth = !checkedType ? await myContract.methods.weth().call() : undefined

    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }
    
    if (allown === '0') {
      return signCall(
        ethereum,
        typeToken,
        ercCntr.methods.approve(cubeAddr,toBN()).encodeABI()
      )
    }
    
    const data = myContract.methods.bridge_transfer(selectedToken, toChainId, address, checkedType, toBNAmount).encodeABI()
    if (token === weth && !checkedType) {
      return signCall(ethereum, cubeAddr, data,  toBNAmount)
    }
    signCall(ethereum, cubeAddr, data)
  }

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.bridge}`}>
        <div className={styles.info}>
          <div className={styles.infoLabel}>From Chain：</div>
          <div className={styles.infoValue}>
            <Select value={formChain} style={{ width: 150 }} onChange={(val) => {
              setFormChain(val)
              setErrorTip(false)
            }}>
              <Option value={256}>HECO</Option>
              <Option value={65}>OEC</Option>
              <Option value={4}>ETH</Option>  
            </Select>
          </div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>Token：</div>
          <div className={styles.infoValue}>
            <Select style={{ width: 150 }} onChange={(val) => { 
              setSelectedToken(val)
             }}>
              {
                tokenList.map(item => (
                  <Option key={item.token} value={item.token}>{item.symbol}</Option>
                ))
              }
            </Select>
          </div>
        </div>
        
        <div className={styles.info}>
          <div className={styles.infoLabel}>To Chain：</div>
          <div className={styles.infoValue}>
            <Select value={toChain} style={{ width: 150 }} onChange={handleToChain}>
              <Option value={256}>HECO</Option>
              <Option value={65}>OEC</Option>
              <Option value={4}>ETH</Option> 
            </Select>
            {errorTip ? <div className={styles.errortip}>To Chain不能与Form Chain相同</div> : null}
          </div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>To Address：</div>
          <div className={styles.infoValue}>
            <Input value={address} onChange={(e) => {
              setAddress(e.target.value)
            }} />
          </div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>存款跨链：</div>
          <div className={styles.infoValue}>
            <Checkbox checked={checkedType} onChange={(e) => { setChecdkType(e.target.checked) }} />
          </div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>Amount：</div>
          <div className={styles.infoValue}>
            <Input value={amount} onChange={(e) => { handleChange(e.target.value) }}/>
            {selectedToken ? <div className={styles.balance} onClick={() => { handleChange(balance) }}>剩余：{balance}</div>: null}
            {
              error ? <div className={styles.errortip}>输入数量不能大于剩余数量</div> : null
            }
          </div>
         
        </div>
        <Button type="primary" size="large" onClick={handleCommit}>提交</Button>
        {
          formChain && selectedToken ? <div className={styles.history} onClick={() => {setVisible(true)}}>查看历史记录</div> : null
        }
        <HistoryModal
          provider={myProvider}
          toChainProvider={toChainProvider}
          toChainContract={toChainContract}
          contract={myContract}
          item={tokenItem}
          visible={visible}
          onCancel={() => { setVisible(false)}}
        />
      </div>
    </>
  )
}

export default Bridge
