import React, { useEffect, useState, useMemo } from 'react'
import uuid from 'uuid/dist/v4'
import { useWeb3React } from '@web3-react/core'
import { Tabs, Table, Modal, Button, Input, message } from 'antd'
import { chains_params, boardAbi, erc20abi } from '../../../public/Web3'
import { toBN, injected, BN_to_str, verifyChainId, verifyAllown } from '../../utils/utils'
import { signCall } from '../../utils/ethRequest'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { TabPane } = Tabs;


const AmountModal = ({
  visible,
  type,
  contract,
  myAmount,
  decimals,
  handleOk,
  handleCancel
}) => {
  const [amount, setAmount] = useState()
  const [balance, setBalance] = useState(null)
  const [error, setError] = useState(false)

  useMemo(async () => {
    const ethereum = window.ethereum;
    
    if (type === 'approve') {
      const balance = await contract.methods.balanceOf(ethereum.selectedAddress).call()
      const maxAmount = BN_to_str(balance, decimals)
      return setBalance(maxAmount)
    }
    setBalance(myAmount)
  }, [type])

  const handleChange = (value) => {
    setError(false)
    if (Number(value) > Number(balance)) return setError(true)
    setAmount(value)
  }
  
  const onOk = () => {
    if (!amount) return handleCancel()
    handleOk(Number(amount))
  }

  return (
    <Modal
      visible={visible}
      onOk={onOk}
      onCancel={() => {
        setAmount('')
        setError(false) 
        handleCancel()
      }}
      getContainer={false}
      closable={false}
    >
      <div className={styles.amount}>
        <Input value={amount} onChange={(e) => { handleChange(e.target.value) }} />
        <div onClick={() => { handleChange(balance) }}>剩余：{balance}</div>
        {
          error ? <div className={styles.errorTip}>输入数量不能大于剩余数量</div> : null
        }
      </div>
      
    </Modal>
  )
}


const Dividendpool = (props) => {

  const columns = [{
    title: 'token',
    dataIndex: 'symbol',
  }, {
    title: '总发放量',
    dataIndex: 'totalReward',
    render: (text, record) => BN_to_str(text, record.decimals)
  }, {
    title: '近30天发放量',
    dataIndex: 'monthReward',
    render: (text, record) => BN_to_str(text, record.decimals)
  }, {
    title: '近1天发放量',
    dataIndex: 'dayReward',
    render: (text, record) => BN_to_str(text, record.decimals)
  }, {
    title: '待提取量',
    dataIndex: 'pendingReward',
    render: (text, record) => BN_to_str(text, record.decimals)
  }]
  const [visible, setVisible] = useState(false)
  const [type, setType] = useState('')
  const [tabKey, setTabKey] = useState('256')
  const [IERC20, setIERC20] = useState(null)
  const [totalAmount, setTotalAmount] = useState(null)
  const [myAmount, setMyAmount] = useState(null)
  const [token, setToken] = useState(null)
  const [tableData, setTableData] = useState([])
  const [decimals, setDecimals] = useState(null)

  const { activate, active } = useWeb3React()

  const handleContect = async () => {
    await activate(injected, walletError => {
      if (walletError.message.includes('user_canceled')) {
        return alert('You canceled the operation, please refresh and try to reauthorize.')
      }
      alert(`Failed to connect: ${walletError.message}`)
    })
  }

  useMemo(() => {
    if (!active) {
      handleContect()
    }
  }, [])

  const {
    boardCntr,
    myProvider,
    boardRoomAddr
  } = useMemo(() => {
    const { provider, boardRoomAddr } = chains_params[tabKey];
    const contract = new provider.eth.Contract(boardAbi, boardRoomAddr);

    return {
      boardCntr: contract,
      myProvider: provider, 
      boardRoomAddr
    }
  }, [tabKey])

  useEffect(() =>　{
    getToken()
  }, [tabKey])
  
  useEffect(() => {
    if (!IERC20) return
    getDecimals()
  }, [IERC20])

  useEffect(() => {
    if (!token || !IERC20 || !decimals) return null;
    getAllAmount()
  }, [token, IERC20, decimals])

  useEffect(() => {
    if (!token) return null;
    getTableData()
  }, [token])

  const getToken = async () => {
    const token = await boardCntr.methods.token().call()
    const IERC20 = new myProvider.eth.Contract(erc20abi, token)
    setToken(token)
    setIERC20(IERC20)
  }

  const getDecimals = async () => {
    const decimals = await IERC20.methods.decimals().call()
    setDecimals(Number(decimals))
  }

  const getAllAmount = async () => { 
    const ethereum = window.ethereum
    const total_amount= await IERC20.methods.balanceOf(boardRoomAddr).call()
    const amount = await boardCntr.methods.userAmount(ethereum.selectedAddress).call()
  
    setMyAmount(BN_to_str(amount, decimals))
    setTotalAmount(BN_to_str(total_amount, decimals))
  }

  const getTableData = async () => {
    
    const ethereum = window.ethereum
    if (!boardCntr || !ethereum.selectedAddress) return

    const tableData = await boardCntr.methods.getPoolInfos(ethereum.selectedAddress).call()

    const formatData = tableData.map(item => {
      const {
        dayReward,
        decimals,
        earnToken,
        monthReward,
        pendingReward,
        symbol,
        totalReward
      } = item;
      return {
        dayReward,
        decimals,
        earnToken,
        monthReward,
        pendingReward,
        symbol,
        totalReward
      }
    })
    setTableData(formatData)
  }
  
  const handleCTRL = async (type) => {
    const ethereum = window.ethereum
    const vaildChainId = await verifyChainId(myProvider, ethereum)
    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }
    if (type === 'approve') {
      const allown = await verifyAllown(IERC20, ethereum.selectedAddress, boardRoomAddr)
      if (allown === '0') {
        return signCall(
          ethereum,
          token,
          IERC20.methods.approve(boardRoomAddr,toBN()).encodeABI()
        )
      }
    }
    setVisible(true)
    setType(type)
  }

  const handleOk = async (amount) => {
    const ethereum = window.ethereum
    const data = type === 'approve' ? boardCntr.methods.deposit(toBN(Number(amount), decimals)).encodeABI() : boardCntr.methods.withdraw(toBN(Number(amount), decimals)).encodeABI()
    signCall(
      ethereum,
      boardRoomAddr,
      data
    ).then(result => {
      if (result) {
        message.success('交易成功')
        setTimeout(() => {
          getAllAmount()
          getTableData()
        }, 5000);
        setVisible(false)
      }
    })
  }

  const handleTake = async () => {
    const ethereum = window.ethereum
    const data = boardCntr.methods.claim().encodeABI()
    const vaildChainId = await verifyChainId(myProvider, ethereum)
    if (!vaildChainId) {
      return message.info('网络已切换，请再试一次')
    }
	  signCall(
      ethereum,
      boardRoomAddr,
      data
    ).then(result => {
      if (result) {
        message.success('交易成功')
      }
    })

  }

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.dividendpool}`}>
        <Tabs
          size="large"
          activeKey={tabKey}
          onChange={key => {
            setTabKey(key)
            // setCtrlFlag(true)
          }}
        >
          <TabPane tab="HECO" key={'256'} />
          <TabPane tab="OEC" key={'65'} />
          <TabPane tab="ETH" key={'4'} />
        </Tabs>
        <div className={styles.info}>
          <div className={styles.infoLabel}>总质押：</div>
          <div className={styles.infoValue}>{totalAmount || '--'}</div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>我的质押：</div>
          <div className={styles.infoValue}>{myAmount || '--'}</div>
        </div>
        <div className={styles.info}>
          <Button type="primary" size='large' onClick={() => {handleCTRL('approve') }}>质押</Button>
          <Button type="primary" size='large' onClick={() => {handleCTRL('withdraw') }}>解押</Button>
          <Button type="primary" size='large' onClick={handleTake}>提取收益</Button>
        </div>
        <Table 
          rowKey={() => uuid()}
          columns={columns}
          dataSource={tableData}
          pagination={false}
        />
        <AmountModal 
          visible={visible}
          type={type}
          contract={IERC20}
          decimals={decimals}
          myAmount={myAmount}
          handleOk={handleOk}
          handleCancel={() => { setVisible(false) }}
        />
      </div>
    </>
  )
}

export default Dividendpool
